<template>
  <div>
    <el-menu
      :default-active="path"
      router
      mode="horizontal"
      background-color="white"
      text-color="#222"
      active-text-color="red"
      style="min-width: 1300px">
      <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
        {{ item.navItem }}
      </el-menu-item>
      <span style="position: absolute;padding-top: 20px;right: 43%;font-size: 20px;font-weight: bold">{{topic}}</span>
      <!-- <el-input
        placeholder="快速搜索..."
        prefix-icon="el-icon-search"
        size="medium"
        style="width: 300px;position:absolute;margin-top: 12px;right: 18%"
        v-model="keywords">
      </el-input> -->
      <div>
        <el-button style="width: 100px;position:absolute;margin-top: 12px;right: 25%" type="primary" @click="ssoLogin" >sso登录</el-button>
       </div>
       <div>
        <el-button style="width: 100px;position:absolute;margin-top: 12px;right: 18%" type="primary" @click="inAdmin" >Admin管理</el-button>
       </div>
       <!-- 登录，显示用户信息；没有检测到登录，不显示用户信息，显示请登录 -->
      <div style="position:absolute;margin-top: 12px;right: 3%">
        <el-avatar  :src="userPicUrl" ></el-avatar>
        <el-link type="danger">{{userNamess}}</el-link>
      </div>
    </el-menu>
  </div>
</template>

<script>
  export default {
    name: 'NavMenu',
    data () {
      return {
        navList: [
          {name: '/index', navItem: '首页'},
          {name: '/jotter', navItem: '公告中心'},
          {name: '/library', navItem: '图书馆'},
          {name: '/personal', navItem: '个人中心(功能开发中)'}
        ],
        keywords: '',
        path: '',
        topic: 'BOOK',
        userNamess: '',
        userPicUrl: ''
      }
    },
    mounted: function () {
      // 获得第一级路由，以设置导航栏高亮
      var x = this.$route.path.indexOf('/', 1)
      if (x !== -1) {
        this.path = this.$route.path.substring(0, x)
      } else {
        this.path = this.$route.path
      }
    },
    computed: {
      hoverBackground () {
        return '#ffd04b'
      }
    },
    methods: {
      inAdmin: function () {
        this.$router.replace('/admin')
      },
      ssoLogin: function () {
        this.$router.replace('/ssoLogin')
      }
    },
    created () {
      this.username = 'resp.data.msg'
      // 请求，通过token获取，用户信息
      this.$axios.get(this.ssourl + '/user/getUserInfo').then(resp => {
            if (resp.data.code === 200) {
              this.userNamess = resp.data.data.username
              this.userPicUrl = resp.data.data.pictureUrl
            } else {
              this.userNamess = resp.data.msg
            }
          })
      .catch(failResponse => {})
    }
  }
</script>

<style scoped>
  a{
    text-decoration: none;
  }

  span {
    pointer-events: none;
  }

</style>



