import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // 这里我们还用到了 localStorage，即本地存储，在项目打开的时候会判断本地存储中是否有
  // user 这个对象存在，如果存在就取出来并获得 username 的值，否则则把 username 设置为空。
  // 这样我们只要不清除缓存，登录的状态就会一直保存
  state: {
    user: {
      name: window.localStorage.getItem('user') == null || window.localStorage.getItem('user') === 'undefined' ? '未登录' : JSON.parse(window.localStorage.getItem('user' || '[]')).name,
      username: window.localStorage.getItem('user') == null || window.localStorage.getItem('user') === 'undefined' ? '' : JSON.parse(window.localStorage.getItem('user' || '[]')).username
    },
    adminMenus: []
  },
  mutations: {
    initAdminMenu (state, menus) {
      state.adminMenus = menus
    },
    login (state, data) {
      state.user = data
      window.localStorage.setItem('user', JSON.stringify(data))
    },
    logout (state) {
      // 注意不能用 null 清除，否则将无法判断 user 里具体的内容
      state.user = []
      window.localStorage.removeItem('user')
      state.adminMenus = []
    }
  },
  actions: {
  }
})



