<template>
  <body id="papersso">
    <el-form :model="loginForm" :rules="rules" class="login-container" label-position="left"
             label-width="0px" v-loading="loading">
      <h3 class="login_title">SSO系统登录</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="loginForm.username"
                  auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password"
                  auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input type="text" v-model="loginForm.code"
                  auto-complete="off" placeholder="验证码"></el-input>
      </el-form-item>
      <el-form-item>
       <span style="display: inline-block;width: 130px;height: 53px;border: 1px solid #D7D7D7;" @click="changeImgCode">
            <img :src="imgCode"  style="width: 100%;height: 100%;cursor: pointer;"/>
        </span>
        </el-form-item>
      <el-checkbox class="login_remember" v-model="checked"
                   label-position="left"><span style="color: #505458">记住密码</span></el-checkbox>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 40%;background: #505458;border: none" v-on:click="login">登录</el-button>
        <router-link to="ssoRegister"><el-button type="primary" style="width: 40%;background: #505458;border: none">注册</el-button></router-link>
      </el-form-item>
    </el-form>
  </body>
</template>
<script>
// 引入 axios 异步请求工具 插件
  export default {
    name: 'SSOLogin',
    data () {
      return {
        rules: {
          username: [{required: true, message: '用户名不能为空', trigger: 'blur'}],
          password: [{required: true, message: '密码不能为空', trigger: 'blur'}]
        },
        checked: true,
        loginForm: {
          username: '',
          password: ''
        },
        loading: false,
        imgCode: 'http://47.105.146.18:8081/dianji.png'
      }
    },
    methods: {
      login () {
        var _this = this
        this.$axios
          .post(this.ssourl + '/user/login', {
            username: this.loginForm.username,
            password: this.loginForm.password,
            code: this.loginForm.code
          })
          .then(resp => {
            if (resp.data.code === 200) {
              var path = _this.$route.query.redirect
              _this.$router.replace({path: path === '/' || path === undefined ? '/library' : path})
            } else {
              this.$alert(resp.data.msg, '提示', {
                confirmButtonText: '确定'
              })
            }
          })
          .catch(failResponse => {})
      },
      changeImgCode () {
            var num = Math.ceil(Math.random() * 10)// 生成一个随机数（防止缓存）
                this.imgCode = this.ssourl + '/user/getCaptcha?userName=' + this.loginForm.username + '&type=1002&num=' + num
            }
      }
    }
</script>
<style>
   #papersso {
    background:url("../../assets/img/bg/subject-bg.jpg") no-repeat;
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
   body{
     margin: -5px 0px;
   }
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }
</style>



