import Vue from 'vue'
import Router from 'vue-router'
import AppIndex from '../components/home/AppIndex'
import Articles from '../components/jotter/Articles'
import Editor from '../components/admin/content/ArticleEditor'
import LibraryIndex from '../components/library/LibraryIndex'
import Login from '../components/Login'
import SSOLogin from '../components/sso/SSOLogin'
import Home from '../components/Home'
import AdminIndex from '../components/admin/AdminIndex'
import Register from '../components/Register'
import SSORegister from '../components/sso/SSORegister'
import ArticleDetails from '../components/jotter/ArticleDetails'

Vue.use(Router)

export default new Router({
  mode: 'history',
  // mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Default',
      redirect: '/index',
      component: AppIndex
    },
    {
      // home页面并不需要被访问，只是作为其它组件的父组件
      path: '/home',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: AppIndex
        },
        {
          path: '/jotter',
          name: 'Jotter',
          component: Articles
        },
        {
          path: '/jotter/article',
          name: 'Article',
          component: ArticleDetails
        },
        {
          path: '/admin/content/editor',
          name: 'Editor',
          component: Editor,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/library',
          name: 'Library',
          component: LibraryIndex
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/ssoLogin',
      name: 'SSOLogin',
      component: SSOLogin
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/ssoRegister',
      name: 'SSORegister',
      component: SSORegister
    },
    {
      path: '/admin',
      name: 'Admin',
      component: AdminIndex,
      meta: {
        requireAuth: true
      }
    }
  ]
})

